@value makerLightPink,makerLightGrey from '../../common/colors.css';

.container {
  width: 100%;
  height: 100%;
  display: flex;
}

.input {
  display: none;
}

.button {
  width: 100%;
  font-weight: bold;
  border: 0;
  cursor: pointer;
  outline: 0;
  font-size: 0.8rem;
}

.button:hover {
  opacity: 0.8;
}

.noImg {
  background-color: makerLightGrey;
}

.selectedImg {
  background-color: makerLightPink;
}
